import { Component, Element, Prop, h } from '@stencil/core';
import { ICON_SIZES } from '../icons/exports';

/**
 * @slot content - for the content of the expand component
 */

@Component({
  tag: 'mdx-expand',
  styleUrl: 'expand.scss',
  shadow: true,
})
export class Expand {
  @Element()
  host: HTMLMdxExpandElement;

  @Prop({ reflect: true })
  expanded = false;

  @Prop()
  labelOpen: string;

  @Prop()
  labelClosed: string;

  @Prop()
  lineClamp: number;

  componentWillRender() {
    document.documentElement.style.setProperty('--expand-line-clamp', this.lineClamp > 1 ? this.lineClamp.toString() : '');
  }

  render() {
    return (
      <div class={`expand${this.expanded ? ' expand--is-expanded' : ''}`}>
        <div class="expand__content">
          <slot />
        </div>
        <button class="expand__button" onClick={() => (this.expanded = !this.expanded)}>
          {this.expanded ? this.labelOpen : this.labelClosed}
          <mdx-icon-chevron-down class="expand__icon" size={ICON_SIZES.XS} />
        </button>
      </div>
    );
  }
}
