:host .expand {
  box-sizing: border-box;
  &__content {
    display: -webkit-box;
    -webkit-box-orient: vertical;
    overflow: hidden;
    font: var(--mdx-comp-expand-font-default);
    font-family: var(--mdx-comp-expand-font-default-font-family);
    font-weight: var(--mdx-comp-expand-font-weight-text);
    line-height: var(--mdx-comp-expand-font-default-line-height);
    font-size: var(--mdx-comp-expand-font-default-font-size);
    letter-spacing: var(--mdx-comp-expand-font-default-letter-spacing);
    text-decoration: var(--mdx-comp-expand-font-default-text-decoration);
    color: var(--mdx-comp-expand-text-color-default);
    word-break: break-all;
    line-clamp: var(--expand-line-clamp);
    -webkit-line-clamp: var(--expand-line-clamp);
    max-height: calc(var(--mdx-comp-expand-font-default-line-height) * var(--expand-line-clamp));

    ::slotted(*) {
      margin: 0;
    }
  }

  &--is-expanded {
    .expand__content {
      animation-name: expand;
      animation-delay: var(--mdx-comp-expand-transition-expand-collapse-delay);
      animation-duration: var(--mdx-comp-expand-transition-expand-collapse-duration);
      animation-iteration-count: 1;
      animation-timing-function: var(--mdx-comp-expand-transition-expand-collapse-timing-function);
      animation-fill-mode: forwards;
      line-clamp: unset;
      -webkit-line-clamp: unset;
    }

    .expand__icon {
      transform: rotate(180deg);
      transition-duration: var(--mdx-comp-expand-transition-icon-chevron-duration);
      transition-delay: var(--mdx-comp-expand-transition-icon-chevron-delay);
      transition-timing-function: var(--mdx-comp-expand-transition-icon-chevron-timing-function);
    }
  }

  &__button {
    background-color: transparent;
    border: none;
    cursor: pointer;
    padding: 0;
    margin-top: var(--mdx-comp-expand-gap-text-label-default);
    display: flex;
    align-items: center;
    gap: var(--mdx-comp-expand-gap-icon-text-default);
    font-family: var(--mdx-comp-expand-font-label-font-family);
    font-weight: var(--mdx-comp-expand-font-weight-label);
    line-height: var(--mdx-comp-expand-font-label-line-height);
    font-size: var(--mdx-comp-expand-font-label-font-size);
    letter-spacing: var(--mdx-comp-expand-font-label-letter-spacing);
    text-decoration: var(--mdx-comp-expand-font-label-text-decoration);
    color: var(--mdx-comp-expand-label-color-default);

    &:hover {
      color: var(--mdx-comp-expand-label-color-hover);
    }
  }

  &__icon {
    transition-property: transform;
    transition-duration: var(--mdx-comp-expand-transition-icon-chevron-duration);
    transition-delay: var(--mdx-comp-expand-transition-icon-chevron-delay);
    transition-timing-function: var(--mdx-comp-expand-transition-icon-chevron-timing-function);
  }
}

@keyframes expand {
  0% {
    max-height: calc(var(--mdx-comp-expand-font-default-line-height) * var(--expand-line-clamp));
  }
  100% {
    max-height: 100vh;
  }
}
